<script>
import { getuserInfoAPI } from "../api/home";
import { ImagePreview } from 'vant';
export default {
  name: "home",
    components: {
    [ImagePreview.Component.name]: ImagePreview.Component,
  },
  data() {
    return {
      loginfo: JSON.parse(sessionStorage.getItem("loginfo")),
      myinfo: [],
    };
  },
  methods: {
    btn(){
        ImagePreview([this.myinfo.avatar])
    },
    //  genggai(){
    //     this.$router.push({name:'genggai',params:{
    //       sex:this.loginfo.userInfo.sex,
    //       birthday:this.loginfo.userInfo.birthday,
    //       nickname:this.loginfo.userInfo.nickname,
    //       avatar:this.loginfo.userInfo.avatar

          
    //       }})
    // },
    tuichu() {
       this.$dialog
        .confirm({
          message: "确定退出登录吗",
        })
        .then(() => {
              sessionStorage.clear();
      this.$toast("退出登录");
      this.$notify({ type: "danger", message: "退出登录" });

      this.loginfo = null;
        })
        .catch(() => {
          this.$toast("取消退出登录");
          this.$notify({ type: "success", message: "取消退出登录" });
        });
      // alert(1)
      
    },
  },
  created() {
    if (this.loginfo !== null) {
      getuserInfoAPI({ token: this.loginfo.token })
        .then((result) => {
          console.log("result: ", result);
          this.myinfo = result.data.userInfo;
        })
        .catch((err) => {
          console.log("err: ", err);
        });
    }
  },
};
</script>
